import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import Antd from 'ant-design-vue'
import 'ant-design-vue/dist/antd.css'
Vue.use(Antd)

//跳转后回到顶部
router.afterEach((to,from,next) => {
  window.scrollTo(0,0);
})

import PCTop from './components/PCTop.vue'
Vue.component("pc-top",PCTop)
import PCBottom from "./components/PCBottom.vue"
Vue.component("pc-bottom",PCBottom)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
